@charset "UTF-8";
body {
  font-family: "游ゴシック Medium", "Yu Gothic Medium", "游ゴシック体", "YuGothic", "ヒラギノ角ゴ ProN W3", "Hiragino Kaku Gothic ProN", "メイリオ", "Meiryo", "verdana", sans-serif;
}

body {
  color: #333;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

h1,
h2,
h3,
h4 {
  font-weight: initial;
  font-size: initial;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.inner {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .inner {
    max-width: 1150px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

.l-sp-contact {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}

.p-contact {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .p-contact {
    display: block;
  }
}

.p-contact__text {
  font-size: 1rem;
}
@media screen and (min-width: 768px) {
  .p-contact__text {
    font-size: 1.875rem;
  }
}

.p-contact__tel a {
  align-items: center;
  display: flex;
  margin-left: 0.625rem;
}
.p-contact__img {
  width: 1.5625rem;
}
@media screen and (min-width: 768px) {
  .p-contact__img {
    width: 2.3125rem;
  }
}

.p-contact__tel-number {
  font-size: 1.25rem;
  font-weight: 700;
  margin-left: 0.3125rem;
}
@media screen and (min-width: 768px) {
  .p-contact__tel-number {
    font-size: 2.1875rem;
  }
}

.p-header-menu__items {
  display: flex;
  height: inherit;
  align-items: center;
  justify-content: center;
}

.p-header-menu__item {
  height: inherit;
}

.p-header-menu__item + .p-header-menu__item {
  position: relative;
}

.p-header-menu__item + .p-header-menu__item::before {
  content: "";
  display: block;
  width: 0.0625rem;
  height: 0.9375rem;
  background-color: #333;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
}
@media screen and (min-width: 768px) {
  .p-header-menu__item + .p-header-menu__item::before {
    height: 1.875rem;
  }
}

.p-header-menu__item a {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  text-decoration: none;
  padding: 0 0.9375rem;
}
@media screen and (min-width: 768px) {
  .p-header-menu__item a {
    padding: 0 2.375rem;
  }
}

.p-header {
  height: 5.0625rem;
  background: #fff;
}
@media screen and (min-width: 768px) {
  .p-header {
    height: 8.375rem;
  }
}

.p-header.change-color {
  background: #111111;
}

.p-header__inner {
  padding: 0 1.25rem;
  height: inherit;
}
@media screen and (min-width: 768px) {
  .p-header__inner {
    display: flex;
    align-items: center;
    padding-left: 1.5625rem;
    padding-right: 1.5625rem;
  }
}

.p-header__logo {
  height: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 0.3125rem;
  border-bottom: 1px solid #139F3E;
}
@media screen and (min-width: 768px) {
  .p-header__logo {
    padding-bottom: 0.625rem;
  }
}

.p-header__logo a {
  display: flex;
  align-items: center;
}

.p-header__title {
  display: block;
  font-size: 1.5625rem;
  font-weight: 700;
}
@media screen and (min-width: 768px) {
  .p-header__title {
    font-size: 2.1875rem;
  }
}

.p-header__sub-title {
  display: block;
  font-size: 1.25rem;
  margin-left: 0.625rem;
}
@media screen and (min-width: 768px) {
  .p-header__sub-title {
    font-size: 1.5625rem;
  }
}

.p-header__logo img {
  width: 1.875rem;
  margin-left: 0.625rem;
}
@media screen and (min-width: 768px) {
  .p-header__logo img {
    width: 3.5625rem;
  }
}

.p-header__pc-menu {
  padding-top: 0.3125rem;
}
@media screen and (min-width: 768px) {
  .p-header__pc-menu {
    padding-top: 0.625rem;
  }
}

.p-header__sub {
  display: none;
}
@media screen and (min-width: 768px) {
  .p-header__sub {
    display: block;
    margin-left: auto;
  }
}

.p-sp-contact {
  border-top: 1px solid #333;
  padding: 0.3125rem;
}
@media screen and (min-width: 768px) {
  .p-sp-contact {
    display: none;
  }
}

.test {
  font-size: 1.25rem;
  background-color: #333;
  color: #fff;
}